___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
}

:root {
  --text-color: rgb(237, 231, 231);
  --text-hover-color: white;

  --footer-height: 80px;
}

body {
  background-color: black;
  margin: 0;
  padding: 0;
  overflow: none;
  height: 100vh;
}

img {
  user-drag: none;
  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

h1 {
  padding: 0;
  margin: 0;
  text-align: center;
}

/*----- Navbar -----*/
nav {
  position: fixed;
  z-index: 2;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.904);

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 20px;
}

nav div {
  display: flex;
  gap: 20px;
}

#title {
  font-size: 1.5rem;
  outline: none;
}

button {
  background: none;
  border: none;

  color: var(--text-color);
  font-size: 1.2rem;
  cursor: pointer;
  border: none;
  outline: 1px solid var(--text-color);
  border-radius: 3px;
  padding: 5px 25px;
}

button:hover {
  color: var(--text-hover-color);
  outline: 2px solid var(--text-hover-color);
}

/*----- general content -----*/
#content {
  overflow: hidden;
}

.page{
  opacity: 0%;
  transition: opacity 0.5s;
}

/*----- homepage -----*/
.home-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.home-container h1 {
  position: absolute;
  z-index: 1;
  color: var(--text-color);
  font-size: 2.5rem;
}

.home-img {
  width: 100vw;
  opacity: 0.4;
  object-fit: cover;
  object-position: center center;
  height: calc(100vh - var(--footer-height));
}

/*----- booking page ----- */
.booking-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.booking-form{
  position: absolute;
  width: 320px;
  background-color: rgb(255, 255, 255);
  border-radius: 5px;

  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
  gap: 30px;
  padding: 40px;
  outline: 2px solid grey;
}

.booking-form div {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.booking-form input {
  height: 30px;
  width: 100%;
  border: none;
  outline: 2px solid rgba(0, 0, 0, 0.232);
  border-radius: 5px;
  flex-basis: auto;
}

.booking-form input:focus, .booking-form input:active{
  outline-color: blue;
}

.booking-form label{
  width: 100px;
  align-self: flex-start;
}

.booking-form .people-input{
  width: 50px;
  align-self: flex-start ;
}

.booking-form button{
  align-self: center;
  color: black;
  outline-color: black;
  width: 100%;
}

  
/* ----- footer ----- */
.footer {
  height: var(--footer-height);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  color: var(--text-color);
}


@media screen and (max-width:500px) {
  
  nav{

  justify-content: center;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;EACE,sBAAsB;AACxB;;AAEA;EACE,gCAAgC;EAChC,yBAAyB;;EAEzB,qBAAqB;AACvB;;AAEA;EACE,uBAAuB;EACvB,SAAS;EACT,UAAU;EACV,cAAc;EACd,aAAa;AACf;;AAEA;EACE,eAAe;EACf,uBAAuB;EACvB,iBAAiB;EACjB,sBAAsB;EACtB,yBAAyB;EACzB,qBAAqB;AACvB;;AAEA;EACE,UAAU;EACV,SAAS;EACT,kBAAkB;AACpB;;AAEA,qBAAqB;AACrB;EACE,eAAe;EACf,UAAU;EACV,WAAW;EACX,sCAAsC;;EAEtC,aAAa;EACb,8BAA8B;EAC9B,eAAe;EACf,aAAa;AACf;;AAEA;EACE,aAAa;EACb,SAAS;AACX;;AAEA;EACE,iBAAiB;EACjB,aAAa;AACf;;AAEA;EACE,gBAAgB;EAChB,YAAY;;EAEZ,wBAAwB;EACxB,iBAAiB;EACjB,eAAe;EACf,YAAY;EACZ,oCAAoC;EACpC,kBAAkB;EAClB,iBAAiB;AACnB;;AAEA;EACE,8BAA8B;EAC9B,0CAA0C;AAC5C;;AAEA,8BAA8B;AAC9B;EACE,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,wBAAwB;AAC1B;;AAEA,uBAAuB;AACvB;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,wBAAwB;EACxB,iBAAiB;AACnB;;AAEA;EACE,YAAY;EACZ,YAAY;EACZ,iBAAiB;EACjB,8BAA8B;EAC9B,0CAA0C;AAC5C;;AAEA,4BAA4B;AAC5B;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;EAClB,YAAY;EACZ,oCAAoC;EACpC,kBAAkB;;EAElB,aAAa;EACb,uBAAuB;EACvB,qBAAqB;EACrB,sBAAsB;EACtB,SAAS;EACT,aAAa;EACb,uBAAuB;AACzB;;AAEA;EACE,WAAW;EACX,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,sBAAsB;AACxB;;AAEA;EACE,YAAY;EACZ,WAAW;EACX,YAAY;EACZ,uCAAuC;EACvC,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,YAAY;EACZ,sBAAsB;AACxB;;AAEA;EACE,WAAW;EACX,uBAAuB;AACzB;;AAEA;EACE,kBAAkB;EAClB,YAAY;EACZ,oBAAoB;EACpB,WAAW;AACb;;;AAGA,uBAAuB;AACvB;EACE,4BAA4B;EAC5B,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,SAAS;EACT,wBAAwB;AAC1B;;;AAGA;;EAEE;;EAEA,uBAAuB;EACvB;AACF","sourcesContent":["* {\n  box-sizing: border-box;\n}\n\n:root {\n  --text-color: rgb(237, 231, 231);\n  --text-hover-color: white;\n\n  --footer-height: 80px;\n}\n\nbody {\n  background-color: black;\n  margin: 0;\n  padding: 0;\n  overflow: none;\n  height: 100vh;\n}\n\nimg {\n  user-drag: none;\n  -webkit-user-drag: none;\n  user-select: none;\n  -moz-user-select: none;\n  -webkit-user-select: none;\n  -ms-user-